phism {\n    background: var(--panel-bg);\n    backdrop-filter: blur(10px);\n    -webkit-backdrop-filter: blur(10px);\n    border: 1px solid var(--border-light);\n}\n\nheader {\n    position: relative;\n    padding: 3rem 0 5rem;\n    margin-bottom: 2rem;\n    overflow: hidden;\n    isolation: isolate;\n}\n\nheader::before {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    background-image: linear-gradient(110deg, #4F46E5, #8B5CF6, #48ecc9, #48ecc9, #48ecc9, #48ecc9);\n    opacity: 0.5;\n    z-index: -1;\n}\n\nheader::after {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 70px;\n    bottom: -1px;\n    left: 0;\n    background-image: linear-gradient(to top, var(--background), transparent);\n    z-index: -1;\n}\n\nh1, h2, h3 {\n    font-weight: 700;\n    letter-spacing: -0.025em;\n}\n\nh1 {\n    font-size: 3.5rem;\n    background: linear-gradient(to right, #fff, #cbd5e1);\n    -webkit-background-clip: text;\n    background-clip: text;\n    color: transparent;\n    margin: 0;\n    text-align: center;\n    animation: fadeUp 1s var(--ease-out) forwards;\n}\n\n@keyframes fadeUp {\n    from {\n        opacity: 0;\n        transform: translateY(20px);\n    }\n    to {\n        opacity: 1;\n        transform: translateY(0);\n    }\n}\n\nh2 {\n    font-size: 1.8rem;\n    color: var(--text-primary);\n    margin-bottom: 1.5rem;\n    position: relative;\n    display: inline-block;\n    animation: fadeIn 0.8s var(--ease-out) forwards;\n}\n\n@keyframes fadeIn {\n    from { opacity: 0; }\n    to { opacity: 1; }\n}\n\nh2::after {\n    content: '';\n    position: absolute;\n    width: 60px;\n    height: 3px;\n    background: var(--gradient-2);\n    bottom: -8px;\n    left: 0;\n    border-radius: 3px;\n    transition: width 0.3s var(--ease-out);\n}\n\nh2:hover::after {\n    width: 100%;\n    background: var(--gradient-1);\n}\n\n.container {\n    max-width: 1280px;\n    margin: 0 auto;\n    padding: 0 2rem;\n}\n\n.dashboard {\n    display: flex;\n    flex-direction: column;\n    gap: 3rem;\n    padding-bottom: 4rem;\n    position: relative;\n}\n\n.fractal-section {\n    margin-bottom: 2rem;\n    animation: slideUpFade 0.7s var(--ease-spring) both;\n}\n\n@keyframes slideUpFade {\n    from {\n        opacity: 0;\n        transform: translateY(40px);\n    }\n    to {\n        opacity: 1;\n        transform: translateY(0);\n    }\n}\n\n.section-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 2rem;\n}\n\n.fractal-grid {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));\n    gap: 2rem;\n    perspective: 1000px;\n}\n\n.fractal-card {\n    background-color: var(--card-bg);\n    border-radius: 16px;\n    overflow: hidden;\n    transition: transform 0.5s var(--ease-spring), \n                box-shadow 0.4s var(--ease-out), \n                background-color 0.3s var(--ease-out);\n    border: 1px solid var(--border-light);\n    transform-origin: center bottom;\n    position: relative;\n    isolation: isolate;\n    animation: floatUp 5s ease-in-out infinite;\n}\n\n@keyframes floatUp {\n    0% { transform: translateY(0px); }\n    50% { transform: translateY(-15px); }\n    100% { transform: translateY(0px); }\n}\n\n.fractal-card:nth-child(2n) {\n    animation-delay: 0.5s;\n}\n\n.fractal-card:nth-child(3n) {\n    animation-delay: 1s;\n}\n\n.fractal-card:nth-child(4n) {\n    animation-delay: 0.3s;\n}\n\n.card-header {\n    padding: 1.25rem 1.5rem;\n    background-color: rgba(0, 0, 0, 0.2);\n    border-bottom: 1px solid var(--border-light);\n    position: relative;\n}\n\n.card-header h3 {\n    margin: 0;\n    font-size: 1.1rem;\n    color: var(--text-primary);\n}\n\n.card-header::after {\n    content: '';\n    position: absolute;\n    left: 0;\n    bottom: -1px;\n    width: 0;\n    height: 1px;\n    background: var(--gradient-2);\n    transition: width 0.3s var(--ease-out);\n}\n\n.fractal-card:hover .card-header::after {\n    width: 100%;\n}\n\ncanvas {\n    width: 100%;\n    height: 250px;\n    display: block;\n    background-color: rgba(0, 0, 0, 0.3);\n    transition: transform 0.3s var(--ease-out), filter 0.3s var(--ease-out);\n}\n\n.fractal-card:hover canvas {\n    filter: brightness(1.1) contrast(1.05);\n}\n\n.fractal-controls {\n    padding: 1.25rem 1.5rem;\n    border-top: 1px solid var(--border-light);\n    transition: background-color 0.3s;\n}\n\n.control-group {\n    margin-bottom: 1rem;\n    transition: transform 0.2s var(--ease-out);\n}\n\n.fractal-card:hover .control-group {\n    transform: translateY(-3px);\n}\n\n.control-group:last-child {\n    margin-bottom: 1.25rem;\n}\n\n.header-content {\n    max-width: 800px;\n    margin: 0 auto;\n    text-align: center;\n    position: relative;\n    z-index: 2;\n}\n\n.subtitle {\n    font-size: 1.2rem;\n    opacity: 0.9;\n    margin-top: 1.5rem;\n}\n\n.header-decoration {\n    width: 100%;\n    max-width: 500px;\n    height: 2px;\n    background: var(--accent);\n    margin: 1.5rem auto;\n    position: relative;\n    overflow: hidden;\n    opacity: 0.6;\n}\n\n.grid-overlay {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-image: linear-gradient(to right, rgba(255, 255, 255, 0.05) 1px, transparent 1px),\n                    linear-gradient(to bottom, rgba(255, 255, 255, 0.05) 1px, transparent 1px);\n    background-size: 40px 40px;\n    z-index: -1;\n    opacity: 0.4;\n    pointer-events: none;\n}\n\n.corner-decoration {\n    position: fixed;\n    width: 200px;\n    height: 200px;\n    z-index: -1;\n    opacity: 0.2;\n    pointer-events: none;\n}\n\n.corner-top-left {\n    top: 0;\n    left: 0;\n    border-top: 2px solid var(--accent);\n    border-left: 2px solid var(--accent);\n    border-top-left-radius: 12px;\n}\n\n.corner-top-right {\n    top: 0;\n    right: 0;\n    border-top: 2px solid var(--secondary);\n    border-right: 