primary: 0 0 20px rgba(79, 70, 229, 0.5);\n    \n    --ease-spring: cubic-bezier(0.2, 0.8, 0.2, 1);\n    --ease-out: cubic-bezier(0.2, 0, 0, 1);\n    --ease-in: cubic-bezier(0.4, 0, 1, 1);\n}\n\n*, *::before, *::after {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n\nhtml {\n    font-size: 16px;\n}\n\nbody {\n    background-color: var(--background);\n    background-image: \n        radial-gradient(circle at 20% 35%, rgba(79, 70, 229, 0.15) 0%, transparent 40%),\n        radial-gradient(circle at 75% 80%, rgba(236, 72, 153, 0.1) 0%, transparent 30%);\n    background-attachment: fixed;\n    color: var(--text-primary);\n    font-family: 'Manrope', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;\n    margin: 0;\n    padding: 0;\n    line-height: 1.5;\n    min-height: 100vh;\n    font-weight: 400;\n    letter-spacing: -0.015em;\n    overflow-x: hidden;\n}\n\n/* Glassmorphism effects */\n.glassmorphism {\n    background: var(--panel-bg);\n    backdrop-filter: blur(10px);\n    -webkit-backdrop-filter: blur(10px);\n    border: 1px solid var(--border-light);\n}\n\nheader {\n    position: relative;\n    padding: 3rem 0 5rem;\n    margin-bottom: 2rem;\n    overflow: hidden;\n    isolation: isolate;\n}\n\nheader::before {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    background-image: linear-gradient(110deg, #4F46E5, #8B5CF6, #48ecc9, #48ecc9, #48ecc9, #48ecc9);\n    opacity: 0.5;\n    z-index: -1;\n}\n\nheader::after {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 70px;\n    bottom: -1px;\n    left: 0;\n    background-image: linear-gradient(to top, var(--background), transparent);\n    z-index: -1;\n}\n\nh1, h2, h3 {\n    font-weight: 700;\n    letter-spacing: -0.025em;\n}\n\nh1 {\n    font-size: 3.5rem;\n    background: linear-gradient(to right, #fff, #cbd5e1);\n    -webkit-background-clip: text;\n    background-clip: text;\n    color: transparent;\n    margin: 0;\n    text-align: center;\n    animation: fadeUp 1s var(--ease-out) forwards;\n}\n\n@keyframes fadeUp {\n    from {\n        opacity: 0;\n        transform: translateY(20px);\n    }\n    to {\n        opacity: 1;\n        transform: translateY(0);\n    }\n}\n\nh2 {\n    font-size: 1.8rem;\n    color: var(--text-primary);\n    margin-bottom: 1.5rem;\n    position: relative;\n    display: inline-block;\n    animation: fadeIn 0.8s var(--ease-out) forwards;\n}\n\n@keyframes fadeIn {\n    from { opacity: 0; }\n    to { opacity: 1; }\n}\n\nh2::after {\n    content: '';\n    position: absolute;\n    width: 60px;\n    height: 3px;\n    background: var(--gradient-2);\n    bottom: -8px;\n    left: 0;\n    border-radius: 3px;\n    transition: width 0.3s var(--ease-out);\n}\n\nh2:hover::after {\n    width: 100%;\n    background: var(--gradient-1);\n}\n\n.container {\n    max-width: 1280px;\n    margin: 0 auto;\n    padding: 0 2rem;\n}\n\n.dashboard {\n    display: flex;\n    flex-direction: column;\n    gap: 3rem;\n    padding-bottom: 4rem;\n    position: relative;\n}\n\n.fractal-section {\n    margin-bottom: 2rem;\n    animation: slideUpFade 0.7s var(--ease-spring) both;\n}\n\n@keyframes slideUpFade {\n    from {\n        opacity: 0;\n        transform: translateY(40px);\n    }\n    to {\n        opacity: 1;\n        transform: translateY(0);\n    }\n}\n\n.section-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 2rem;\n}\n\n.fractal-grid {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));\n    gap: 2rem;\n    perspective: 1000px;\n}\n\n.fractal-card {\n    background-color: var(--card-bg);\n    border-radius: 16px;\n    overflow: hidden;\n    transition: transform 0.5s var(--ease-spring), \n                box-shadow 0.4s var(--ease-out), \n                background-color 0.3s var(--ease-out);\n    border: 1px solid var(--border-light);\n    transform-origin: center bottom;\n    position: relative;\n    isolation: isolate;\n    animation: floatUp 5s ease-in-out infinite;\n}\n\n@keyframes floatUp {\n    0% { transform: translateY(0px); }\n    50% { transform: translateY(-15px); }\n    100% { transform: translateY(0px); }\n}\n\n.fractal-card:nth-child(2n) {\n    animation-delay: 0.5s;\n}\n\n.fractal-card:nth-child(3n) {\n    animation-delay: 1s;\n}\n\n.fractal-card:nth-child(4n) {\n    animation-delay: 0.3s;\n}\n\n.card-header {\n    padding: 1.25rem 1.5rem;\n    background-color: rgba(0, 0, 0, 0.2);\n    border-bottom: 1px solid var(--border-light);\n    position: relative;\n}\n\n.card-header h3 {\n    margin: 0;\n    font-size: 1.1rem;\n    color: var(--text-primary);\n}\n\n.card-header::after {\n    content: '';\n    position: absolute;\n    left: 0;\n    bottom: -1px;\n    width: 0;\n    height: 1px;\n    background: var(--gradient-2);\n    transition: width 0.3s var(--ease-out);\n}\n\n.fractal-card:hover .card-header::after {\n    width: 100%;\n}\n\ncanvas {\n    width: 100%;\n    height: 250px;\n    display: block;\n    background-color: rgba(0, 0, 0, 0.3);\n    transition: transform 0.3s var(--ease-out), filter 0.3s var(--ease-out);\n}\n\n.fractal-card:hover canvas {\n    filter: brightness(1.1) contrast(1.05);\n}\n\n.fractal-controls {\n    padding: 1.25rem 1.5rem;\n    border-top: 1px solid var(--border-light);\n    transition: background-color 0.3s;\n}\n\n.control-group {\n    margin-bottom: 1rem;\n    transition: transform 0.2s var(--ease-out);\n}\n\n.fractal-card:hover .control-group {\n    transform: translateY(-3px);\n}\n\n.control-group:last-child {\n    margin-bottom: 1.25rem;\n}\n\n.header-content {\n    max-width: 800px;\n    margin: 0 auto;\n    text-align: center;\n    position: relative;\n    z-index: 2;\n}\n\n.subtitle {\n    font-size: 1.2rem;\n    opacity: 0.9;\n    margin-top: 1.5rem;\n}\n\n.header-decoration {\n    width: 100%;\n    max-width: 500px;\n    height: 2px;\n    background: var(--accent);\n    margin: 1.5rem auto;\n    position: relative;\n    overflow: hidden;\n    opacity: 0.6;\n}\n\n.grid-overlay {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-image: linear-gradient(to right, rgba(255, 255, 255, 0.05) 1px, transparent 1px),\n                    linear-gradient(to bottom, rgba(255, 255, 255, 0.05) 1px, transparent 1px);\n    background-size: 40px 40px;\n    z-index: -1;\n    opacity: 0.4;\n    pointer-events: none;\n}\n\n.corner-decoration {\n    position: fixed;\n    width: 200px;\n    height: 200px;\n    z-index: -1;\n    opacity: 0.2;\n    pointer-events: none;\n}\n\n.corner-top-left {\n    top: 0;\n    left: 0;\n    border-top: 2px solid var(--accent);\n    border-left: 2px solid var(--accent);\n    border-top-left-radius: 12px;\n}\n\n.corner-top-right {\n    top: 0;\n    right: 0;\n    border-top: 2px solid var(--secondary);\n    border-right: 2px solid var(--secondary);\n    border-top-right-radius: 12px;\n}\n\n.corner-bottom-left {\n    bottom: 0;\n    left: 0;\n    border-bottom: 2px solid var(--secondary);\n    border-left: 2px solid var(--secondary);\n    border-bottom-left-radius: 12px;\n}\n\n.corner-bottom-right {\n    bottom: 0;\n    right: 0;\n    border-bottom: 2px solid var(--accent);\n    border-right: 2px solid var(--accent);\n    border-bottom-right-radius: 12px;\n}\n\n.highlight {\n    color: var(--accent);\n    font-weight: 600;\n    position: relative;\n    z-index: 1;\n}\n\n.footer {\n    padding: 3rem 0;\n    margin-top: 2rem;\n    text-align: center;\n    color: var(--text-secondary);\n    position: relative;\n}\n\n.footer-line {\n    height: 1px;\n    width: 100%;\n    background: linear-gradient(to right, transparent, var(--border-light), transparent);\n    margin-bottom: 2rem;\n}\n\n@media (max-width: 768px) {\n    .fractal-grid {\n        grid-template-columns: 1fr;\n    }\n    \n    h1 {\n        font-size: 2.25rem;\n    }\n    \n    h2 {\n        font-size: 1.5rem;\n    }\n    \n    header {\n        padding: 2rem 0 4rem;\n    }\n    \n    .container {\n        padding: 0 1.25rem;\n    }\n} "],"sourceRoot":""}]);
