79, 70, 229, 0.3);\r\n}\r\n\r\n/* Label styles */\r\nlabel {\r\n  display: block;\r\n  margin-bottom: 0.5rem;\r\n  color: var(--text-secondary);\r\n  font-size: 0.9rem;\r\n  font-weight: 500;\r\n}\r\n\r\n/* Button styling */\r\nbutton {\r\n  background: var(--primary);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 8px;\r\n  padding: 0.75rem 1.25rem;\r\n  font-family: inherit;\r\n  font-weight: 500;\r\n  font-size: 0.95rem;\r\n  cursor: pointer;\r\n  transition: all 0.3s var(--ease-out);\r\n  position: relative;\r\n  overflow: hidden;\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 0.5rem;\r\n  z-index: 1;\r\n}\r\n\r\nbutton:hover {\r\n  background: var(--primary-hover);\r\n  transform: translateY(-2px);\r\n  box-shadow: var(--shadow-sm);\r\n}\r\n\r\nbutton:active {\r\n  transform: translateY(0);\r\n  background: var(--primary-active);\r\n}\r\n\r\nbutton::before {\r\n  content: '';\r\n  position: absolute;\r\n  top: 0;\r\n  left: -100%;\r\n  width: 100%;\r\n  height: 100%;\r\n  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);\r\n  z-index: -1;\r\n  transition: 0.5s;\r\n}\r\n\r\nbutton:hover::before {\r\n  left: 100%;\r\n}\r\n\r\n/* Range value display */\r\n.range-value {\r\n  display: inline-block;\r\n  font-family: monospace;\r\n  font-size: 0.9rem;\r\n  color: var(--text-primary);\r\n  background: var(--surface-1);\r\n  padding: 0.25rem 0.5rem;\r\n  border-radius: 4px;\r\n  min-width: 60px;\r\n  text-align: center;\r\n  margin-left: 0.5rem;\r\n}\r\n\r\n/* Input group styling */\r\n.input-group {\r\n  margin-bottom: 1.25rem;\r\n}\r\n\r\n.input-row {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n/* Fractal tooltip/modal styles */\r\n.fractal-tooltip {\r\n  position: fixed;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  width: 90%;\r\n  max-width: 700px;\r\n  background: var(--panel-bg);\r\n  backdrop-filter: blur(20px);\r\n  -webkit-backdrop-filter: blur(20px);\r\n  border-radius: 16px;\r\n  border: 1px solid var(--border-medium);\r\n  box-shadow: var(--shadow-lg), var(--glow-primary);\r\n  z-index: 1000;\r\n  animation: modalFadeIn 0.3s var(--ease-spring);\r\n  overflow: hidden;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n@keyframes modalFadeIn {\r\n  from {\r\n    opacity: 0;\r\n    transform: translate(-50%, -48%) scale(0.95);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: translate(-50%, -50%) scale(1);\r\n  }\r\n}\r\n\r\n.fractal-tooltip-header {\r\n  padding: 1.25rem 1.5rem;\r\n  background: rgba(0, 0, 0, 0.25);\r\n  border-bottom: 1px solid var(--border-medium);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.fractal-tooltip-title {\r\n  font-size: 1.5rem;\r\n  font-weight: 600;\r\n  margin: 0;\r\n  color: var(--text-primary);\r\n  background: var(--gradient-1);\r\n  -webkit-background-clip: text;\r\n  background-clip: text;\r\n  color: transparent;\r\n}\r\n\r\n.fractal-tooltip-close {\r\n  background: none;\r\n  border: none;\r\n  color: var(--text-secondary);\r\n  font-size: 1.5rem;\r\n  cursor: pointer;\r\n  width: 2rem;\r\n  height: 2rem;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 50%;\r\n  transition: all 0.2s var(--ease-out);\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n.fractal-tooltip-close:hover {\r\n  background: rgba(236, 72, 153, 0.2);\r\n  color: var(--text-primary);\r\n  transform: none;\r\n  box-shadow: none;\r\n}\r\n\r\n.fractal-tooltip-content {\r\n  padding: 2rem;\r\n  overflow-y: auto;\r\n  max-height: 70vh;\r\n}\r\n\r\n.fractal-tooltip-description {\r\n  color: var(--text-secondary);\r\n  font-size: 1rem;\r\n  line-height: 1.6;\r\n  margin-bottom: 2rem;\r\n  background: rgba(15, 23, 42, 0.4);\r\n  padding: 1.5rem;\r\n  border-radius: 12px;\r\n  border: 1px solid var(--border-light);\r\n}\r\n\r\n.fractal-tooltip-description p {\r\n  margin: 0 0 1rem 0;\r\n}\r\n\r\n.fractal-tooltip-description p:last-child {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.fractal-formula-container {\r\n  background: rgba(15, 23, 42, 0.4);\r\n  border-radius: 12px;\r\n  padding: 1.5rem;\r\n  position: relative;\r\n  margin-bottom: 1rem;\r\n  border: 1px solid var(--border-light);\r\n}\r\n\r\n.formula-title {\r\n  font-size: 0.9rem;\r\n  color: var(--text-secondary);\r\n  margin-bottom: 0.75rem;\r\n  font-weight: 600;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.fractal-formula {\r\n  font-family: monospace;\r\n  color: var(--primary);\r\n  font-size: 0.95rem;\r\n  line-height: 1.6;\r\n  white-space: pre-wrap;\r\n  overflow-x: auto;\r\n  background: rgba(15, 23, 42, 0.6);\r\n  padding: 1rem;\r\n  border-radius: 8px;\r\n  border: 1px solid var(--border-light);\r\n}\r\n\r\n.formula-copy-button {\r\n  position: absolute;\r\n  top: 0.75rem;\r\n  right: 0.75rem;\r\n  background: rgba(79, 70, 229, 0.2);\r\n  border: 1px solid rgba(79, 70, 229, 0.3);\r\n  color: var(--text-primary);\r\n  font-size: 0.8rem;\r\n  padding: 0.35rem 0.7rem;\r\n  border-radius: 6px;\r\n  cursor: pointer;\r\n  transition: all 0.2s var(--ease-out);\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.25rem;\r\n  opacity: 0.8;\r\n}\r\n\r\n.formula-copy-button:hover {\r\n  background: rgba(79, 70, 229, 0.3);\r\n  transform: translateY(-2px);\r\n  opacity: 1;\r\n}\r\n\r\n.formula-copied {\r\n  position: absolute;\r\n  top: 0.75rem;\r\n  right: 0.75rem;\r\n  background: rgba(16, 185, 129, 0.2);\r\n  color: var(--success);\r\n  border: 1px solid rgba(16, 185, 129, 0.3);\r\n  font-size: 0.8rem;\r\n  padding: 0.35rem 0.7rem;\r\n  border-radius: 6px;\r\n  opacity: 0;\r\n  pointer-events: none;\r\n}\r\n\r\n.formula-copied.visible {\r\n  animation: fadeInOut 1.5s var(--ease-out);\r\n}\r\n\r\n@keyframes fadeInOut {\r\n  0% { opacity: 0; }\r\n  20% { opacity: 1; }\r\n  80% { opacity: 1; }\r\n  100% { opacity: 0; }\r\n}\r\n\r\n.fractal-modal-backdrop {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background: rgba(0, 0, 0, 0.5);\r\n  backdrop-filter: blur(4px);\r\n  -webkit-backdrop-filter: blur(4px);\r\n  z-index: 999;\r\n  animation: backdropFadeIn 0.3s var(--ease-out);\r\n}\r\n\r\n@keyframes backdropFadeIn {\r\n  from { opacity: 0; }\r\n  to { opacity: 1; }\r\n}\r\n\r\n/* Editor styles */\r\n.editor-container {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 2rem;\r\n  margin-bottom: 3rem;\r\n}\r\n\r\n.editor-panel {\r\n  background: var(--card-bg);\r\n  border-radius: 16px;\r\n  border: 1px solid var(--border-light);\r\n  overflow: hidden;\r\n}\r\n\r\n.editor-header {\r\n  padding: 1.25rem 1.5rem;\r\n  background: rgba(0, 0, 0, 0.2);\r\n  border-bottom: 1px solid var(--border-light);\r\n}\r\n\r\n.editor-header h3 {\r\n  margin: 0;\r\n  font-size: 1.2rem;\r\n  color: var(--text-primary);\r\n}\r\n\r\n.editor-controls {\r\n  padding: 1.5rem;\r\n}\r\n\r\n.editor-canvas-container {\r\n  width: 100%;\r\n  position: relative;\r\n}\r\n\r\n.editor-canvas {\r\n  width: 100%;\r\n  height: 350px;\r\n  display: block;\r\n  background-color: rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n/* For tablet and below */\r\n@media (max-width: 1024px) {\r\n  .editor-container {\r\n    grid-template-columns: 1fr;\r\n  }\r\n}\r\n\r\n/* For small screens */\r\n@media (max-width: 640px) {\r\n  .fractal-tooltip {\r\n    width: 95%;\r\n    max-height: 90vh;\r\n  }\r\n  \r\n  .fractal-tooltip-content {\r\n    padding: 1.5rem;\r\n  }\r\n} "],"sourceRoot":""}]);
